<template>
  <div class="descripcion-content" v-show="boolVista">
    <div class="container">
      <div v-if="finish">
        <div class="card row  pt-32">
          <div class="col-lg-12 text-center mt-2">
            <h4 style="color: #0078cf">Se generó la solicitud Nro. {{ this.idSolicitud }}</h4>

          </div>
          <div class="col-sm-10"></div>

          <div class="mt-5"></div>
          <div class="form-inline align-self-center mt-2">
            <div class="mx-lg-5 mt-2 text-center ">
              <img src="../../assets/tengaEnCuenta.png" height="55" width="55"/><a :href="urlPdfDescargar" target="_blank"><u><b>Descargar en formato PDF</b></u></a>
              <div class="form-control-plaintext" />
            </div>
            <div class="form-group mx-sm-3 mb-2">
              <label class="sr-only">Password</label>
              <input type="text" v-model="enailEnviar" class="form-control" style="width: 350px" placeholder="Ingrese su correo para el envío de la constancia"/>
            </div>
            <button v-on:click="enviarCorreo()" class="btn btn-default">Enviar copia</button>
          </div>
          <div class="mt-4"></div>

        </div>
      </div>

      <div v-else="finish">
        <div class="card row  pt-32">
          <div class="top-title">
            <div class="col-lg-12">
              <h3 class="titulo-form-regitroAcceso  pb-32">Formulario de Registro de Solicitudes de Acceso a la
                Información Pública</h3>
            </div>
          </div>

          <div class="col-12">
            <b-card no-body style="border:none;outline:none !important;box-shadow: none;">
              <div class="form-group col-lg-12">
                <div class="row d-flex justify-content-between text-align-left">
                  <div class="col-lg-3" style="padding-left:0px">
                    <a style="text-align:left"><b>Fecha de registro: </b> {{ customFormatter(this.date) }}</a>
                  </div>
                  <div class="col-lg-3" style="padding-left:0px">
                    <a style="text-align:left"><b>Nro. Solicitud: </b> {{ this.solicitud }} - {{ this.anho }}</a>
                  </div>
                </div>
              </div>
            </b-card>
          </div>

          <div class="col-12">
            <div class="alert alert-info mt-3" role="alert" v-html="descripcionTipoTramite"></div>
          </div>

          <div class="col-12">
            <b-card header="DATOS DEL SOLICITANTE">
              <div class="row">
                <div class="col-12">
                  <div class="row">
                    <div class="form-group col-12  col-lg-4">
                      <label style="margin-top:10px;padding-right:5px">Tipo de Documento(*): </label>
                      <b-form-radio-group v-model="tipoDoc" style="display: inline-block;">
                        <b-form-radio name="radio-size" v-model="rdTipoDoc" value="31" size="sm"
                                      @change="rdPersona=109">DNI
                        </b-form-radio>
                        <b-form-radio name="radio-size" v-model="rdTipoDoc" value="33" size="sm"
                                      @change="rdPersona=109">CE
                        </b-form-radio>
                        <b-form-radio name="radio-size" v-model="rdTipoDoc" value="32" size="sm">RUC</b-form-radio>
                      </b-form-radio-group>
                    </div>
                    <div class="form-group col-12 col-lg-8">
                      <b-input-group>
                        <b-form-input v-if="rdTipoDoc==31" placeholder="Ingresa tu documento" v-model="nroDoc"
                                      @input="v => {nroDoc = v.toUpperCase()}" maxlength="8"
                                      type="search"></b-form-input>
                        <b-form-input v-if="rdTipoDoc==32" placeholder="Ingresa tu documento" v-model="nroDoc"
                                      @input="v => {nroDoc = v.toUpperCase()}" maxlength="11"
                                      type="search"></b-form-input>
                        <b-form-input v-if="rdTipoDoc==33" placeholder="Ingresa tu documento" v-model="nroDoc"
                                      @input="v => {nroDoc = v.toUpperCase()}" maxlength="12"
                                      type="search"></b-form-input>
                      </b-input-group>
                    </div>
                  </div>
                  <div class="row">
                    <div class="form-group col-sm-4">
                      <label style="margin-top:10px;padding-right:5px">Tipo de Persona(*): </label>
                      <b-form-radio-group v-model="tipoPer" style="display: inline-block;">
                        <b-form-radio name="radio-size" v-model="rdPersona" :value=109 size="sm">Natural</b-form-radio>
                        <b-form-radio name="radio-size" v-model="rdPersona" :value=110 size="sm"
                                      :disabled="(rdTipoDoc==31 || rdTipoDoc==33)">Jurídica
                        </b-form-radio>
                      </b-form-radio-group>
                    </div>

                    <template v-if="rdPersona">
                      <div class="col-sm-8" v-if="rdPersona==109">
                        <div class="row">
                          <div class="form-group col-sm-4">
                            <b-input-group>
                              <b-form-input placeholder="Nombre(s)" v-model="nombre"
                                            @input="v => {nombre = v.toUpperCase()}"></b-form-input>
                            </b-input-group>
                          </div>
                          <div class="form-group col-sm-4">
                            <b-input-group>
                              <b-form-input placeholder="Ape. paterno" v-model="aPaterno"
                                            @input="v => {aPaterno = v.toUpperCase()}"></b-form-input>
                            </b-input-group>
                          </div>
                          <div class="form-group col-sm-4">
                            <b-input-group>
                              <b-form-input placeholder="Ape. materno" v-model="aMaterno"
                                            @input="v => {aMaterno = v.toUpperCase()}"></b-form-input>
                            </b-input-group>
                          </div>
                        </div>
                      </div>
                      <div class="form-group col-sm-8" v-if="rdPersona==110">
                        <div class="row">
                          <div class="form-group col-sm-12">
                            <b-input-group>
                              <b-form-input placeholder="Razón Social (*):" v-model="rSocial"
                                            type="search"></b-form-input>
                            </b-input-group>
                          </div>
                        </div>
                      </div>
                    </template>
                  </div>
                  <div class="row">
                    <div class=" col-12  col-lg-4">
                      <div>
                        <div><label>Domicilio Legal(*):</label></div>
                        <b-form-input v-model="domicilio" @input="v => {domicilio = v.toUpperCase()}"></b-form-input>
                      </div>
                      <div class="form-group ">
                        <b-input-group>
                        </b-input-group>
                      </div>
                    </div>
                    <div class="col-12  col-lg-8">
                      <div class="row">
                        <div class="col-12  col-lg-4" v-if="mapListaDepartamento">
                          <div><label>Departamento:</label></div>
                          <model-select
                            :options="mapListaDepartamento"
                            v-model="itemDepartamento"
                            placeholder="Seleccione un Departamento"
                            @input="llenarProvincia"
                          ></model-select>
                        </div>

                        <div class="col-12  col-lg-4" v-if="mapListaProvincia">
                          <div>
                            <label>Provincia:</label>
                          </div>
                          <model-select
                            :options="mapListaProvincia"
                            v-model="itemProvincia"
                            placeholder="Seleccione una Provincia"
                            @input="llenarDistrito"
                          ></model-select>
                        </div>

                        <div class="col-12  col-lg-4" v-if="mapListaDistrito">
                          <div>
                            <label>Distrito:</label>
                          </div>
                          <model-select
                            :options="mapListaDistrito"
                            v-model="itemDistrito"
                            placeholder="Seleccione un Distrito"
                          ></model-select>
                        </div>
                      </div>
                    </div>
                  </div>

                  <div class="alert alert-info mt-3" role="alert" v-html="descripcionDatosAdiconales"></div>
                  <div class="row" style="margin-top:5px">
                    <div class="form-group col-sm-12">
                      <div class="row">
                        <div class="form-group col-sm-3">
                          <div>
                            <label>Teléfono:</label>
                          </div>
                          <b-input-group>
                            <b-form-input v-model="telefono" type="search"></b-form-input>
                          </b-input-group>
                        </div>
                        <div class="form-group col-sm-3">
                          <div>
                            <label>Celular:</label>
                          </div>
                          <b-input-group>
                            <b-form-input v-model="celular" @keypress="isNumber($event)" type="tel"></b-form-input>
                          </b-input-group>
                        </div>
                        <div class="form-group col-sm-6">
                          <div>
                            <label>Correo:</label>
                          </div>
                          <b-input-group>
                            <b-form-input v-model="correo" type="email"></b-form-input>
                          </b-input-group>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>

              </div>

            </b-card>
          </div>
          <br/>

          <div class="col-12">
            <b-card no-body style="padding:0px;"
                    header="Presentación y formalidades de la solicitud (Art. 10 del Reglamento de la Ley Nº 27806)">
              <div class="alert alert-info " style="margin:0px" role="alert" v-html="descripcionPresentacion"></div>
            </b-card>
          </div>
          <br/>

          <div class="col-12">
            <b-card no-body style="padding:0px"
                    header="INFORMACIÓN QUE POSEE Y PRODUCE LA MUNICIPALIDAD DISTRITAL DE MIRAFLORES A CONSULTAR (*)">
              <b-form-textarea v-model="informacion" rows="6"
                               style="border:none;outline:none !important;box-shadow: none;"></b-form-textarea>
            </b-card>
          </div>
          <br/>

          <div class="col-12">
            <b-card header="ARCHIVO COMPLEMENTARIO A LA SOLICITUD">
              <div class="row">
                <div class="custom-file col-9 col-lg-10">
                  <input type="file" class="custom-file-input" id="customFileLang" lang="es" @change="onFileSelected">
                  <label class="custom-file-label" for="customFileLang"
                         placeholder="Seleccionar Archivo">{{ adjunto.nombre }}</label>
                </div>
                <div class="col-3  col-lg-2">
                  <b-button variant="light" class="btn-eliminar-SIP  d-lg-none" type="button" title="Eliminar"
                            @click.prevent="eliminar()">
                    <img width="17" :src="require(`../../assets/delete.png`)"/>
                  </b-button>
                  <b-button variant="light" class="btn-eliminar-SIP  d-none d-lg-block" type="button" title="Eliminar"
                            @click.prevent="eliminar()">Eliminar
                  </b-button>
                </div>
              </div>
            </b-card>
          </div>
          <br/>

          <div class="col-12">
            <b-card no-body style="padding:0px"
                    header="GERENCIA / SUBGERENCIA POSEEDORA DE LA INFORMACIÓN (SELECCIONES (TODAS) EN CASO DE NO CONOCERLO)">
              <div class="col-xs-12 col-md-12 col-lg-12" style="padding:0px">
                <div>
                  <multiselect :multiple="false" :close-on-select="true" :clear-on-select="false"
                               :preserve-search="false" :options="dataUnidades"
                               v-model="unidadOrigen" deselect-label="Quitar" select-label="Seleccionar" track-by="text"
                               label="text" placeholder="Seleccione una Unidad.">
                    <template slot="singleLabel" slot-scope="{ option }">
                      <strong>{{ option.text }}</strong>
                    </template>
                  </multiselect>
                </div>
              </div>
            </b-card>
          </div>
          <br/>
          <div class="col-12">
            <b-card no-body style="padding:0px" header="FORMA DE ENTREGA DE LA INFORMACIÓN (*)">
              <div class="row" style="margin-left:10px;margin-top:5px">
                <div class="form-group col-sm-8" style="padding:0px">
                  <b-form-radio-group style="margin-left:20px;margin-top:10px">
                    <b-form-radio name="radio-size" v-model="rdEntrega" value="317" size="sm">Copia Simple Formato
                    </b-form-radio>
                    <b-form-radio name="radio-size" v-model="rdEntrega" value="318" size="sm">Información en CD
                    </b-form-radio>
                    <b-form-radio name="radio-size" v-model="rdEntrega" value="319" size="sm">Información por correo
                      electrónico
                    </b-form-radio>
                  </b-form-radio-group>
                </div>
              </div>
              <div class="alert alert-info" style="margin:0px" role="alert" v-html="descripcionEntrega"></div>
            </b-card>
          </div>
          <br/>

          <div class="col-12">
            <b-card no-body style="padding:0px" header="REQUISITOS CONTENIDOS EN EL TUPA">
              <div class="alert alert-info" style="margin:0px" role="alert" v-html="descripcionRequisitos"></div>
            </b-card>
          </div>
          <br/>

          <div class="col-12">
            <b-card no-body style="padding:0px" header="OBSERVACIONES (OPCIONAL)">
              <b-form-textarea v-model="observaciones" rows="6"
                               style="border:none;outline:none !important;box-shadow: none;"></b-form-textarea>
            </b-card>
          </div>
          <br/>

          <div class="col-12">
            <div class="form-group  box-btn-registroAcceso">
              <b-button class="btn-registroAcceso" @click.prevent="IniciarTramite()" variant="primary">Enviar
                Solicitud
              </b-button>
            </div>
          </div>
          <br/>
        </div>
      </div>
    </div>
  </div>
</template>
<script>

import {ModelSelect} from "vue-search-select";
import Datepicker from "vuejs-datepicker";
import Util from "../../tools/util";
import axios from "axios";
import EventBus from "../../helpers/EventBus.js"
import Constantes from "../../store/constantes.js";
import Multiselect from 'vue-multiselect';
import moment from "moment";
import {es} from 'vuejs-datepicker/dist/locale';
import _ from "lodash"

export default {
  components: {Multiselect, Datepicker, ModelSelect},
  data() {
    return {
      descripcionTipoTramite: "",
      descripcionPresentacion: "",
      descripcionRequisitos: "",
      descripcionEntrega: "",
      descripcionDatosAdiconales: "",
      date: new Date,
      salto: `<br>`,
      tipoPer: 109,
      rdPersona: 109,
      rdTipoDoc: 31,
      rdEntrega: "",
      tipoDoc: 31,
      rSocial: "",
      nombre: "",
      aPaterno: "",
      aMaterno: "",
      nroDoc: "",
      domicilio: "",
      telefono: "",
      celular: "",
      correo: "",
      informacion: "",
      observaciones: "",
      dataUnidades: [],
      unidadOrigen: {},
      itemDepartamento: 14,
      itemProvincia: 1,
      itemDistrito: 1392,
      mapListaDepartamento: null,
      mapListaProvincia: null,
      mapListaDistrito: null,
      boolFormulario: false,
      boolVista: true,
      correoAcceso: "ACCESO.INFORMACION@MIRAFLORES.GOB.PE",
      texto: "",
      solicitud: "",
      anho: "",
      cDigitos: 0,
      adjunto: {nombre: null, archivo: null},
      cargado: false,
      nombArchivo: '',
      finish: false,
      enailEnviar: "",
      urlPdfDescargar: "",
    };
  },
  created() {
    window.document.title = "Registro"

    this.descripcionTipoTramite = "SOLICITUD DE ACCESO A LA INFORMACIÓN PÚBLICA CREADA U OBTENIDA POR LA ENTIDAD, QUE SE ENCUENTRE EN SU POSESIÓN O BAJO SU CONTROL" +
      "(Decreto Supremo N° 164 -2020-PCM que aprueba el Procedimiento Administrativo Estandarizado de Acceso a la Información Pública creada u obtenida por la entidad, que se encuentre en su posesión o bajo su control).";
    this.descripcionPresentacion = "<b>TEXTO:</b> 'La solicitud de acceso a la información pública puede ser presentada por cualquier persona natural o jurídica ante la unidad de recepción documentaria de la entidad, a través de su Portal de Transparencia, a través de una dirección electrónica establecida para tal fin o a través de cualquier otro medio idóneo que para tales efectos establezcan las Entidades " +
      "El uso de formato contenido en el Anexo del presente Reglamento es opcional para el/la solicitante, quien podrá utilizar cualquier otro medio idóneo para transmitir su solicitud que contenga la siguiente información: a. Nombres y apellidos completos, número del documento de identificación que corresponda y domicilio. Tratándose de menores de edad no será necesario consignar el número del documento de identidad; b. De ser el caso, número de teléfono y/o correo electrónico; " +
      "c. En caso que la solicitud se presente en la unidad de recepción documentaria de la Entidad, la solicitud debe contener firma del/de la solicitante o huella digital, de no saber firmar o estar impedido/a de hacerlo; d. Expresión concreta y precisa del pedido de información, asÃ­ como cualquier otro dato que propicie la localización o facilite la búsqueda de la información solicitada; y e. En caso el/la solicitante conozca la dependencia que posea la información, deberá indicarlo en la solicitud; " +
      " y f. Opcionalmente, la forma o modalidad en la que prefiere el/la solicitante que la Entidad le entregue la información de conformidad con lo dispuesto en la Ley. Si el/la solicitante no hubiese incluido el nombre del/de la funcionario/a responsable o lo hubiera hecho de forma incorrecta, las unidades de recepción documentaria de las entidades deberán canalizar la solicitud al/a la funcionario/a responsable." +
      "Las formalidades establecidas en este artí­culo tienen como finalidad garantizar la satisfacción del derecho de acceso a la información pública, por lo que deben interpretarse en forma favorable a la admisión y decisión final de las pretensiones del/de la solicitante'. (se incluyó el lenguaje inclusivo en el texto).";
    this.descripcionRequisitos = "1. Solicitud presentada ví­a formulario o documento que contenga la misma información. <br/>" +
      "2. De corresponder indicar número y fecha de comprobante de pago, en caso el pago se haya efectuado en la entidad. Si el pago se realizó en el Banco de la Nación, adjuntar copia del comprobante de pago*. <br/>" +
      "* Este requisito se presenta posteriormente al ingreso de la solicitud. La entidad a partir del sexto dí­a hábil de presentada la solicitud, pone a disposición del ciudadano el costo de reproducción de la información requerida a cancelar. <br/>" +
      "<b>Notas:</b> <br/>" +
      " - Solicitud de información dirigida al Responsable de Acceso a la Información Pública. En caso de que este no hubiese sido designado, la solicitud se dirige al funcionario que tiene en su poder la información requerida o al superior inmediato. <br/>" +
      " - La solicitud puede ser presentada a través del Portal de Transparencia de la Entidad, de forma personal ante la unidad de recepción documentaria, o a través de otros canales creados para tal fin. <br/>" +
      " - La liquidación del costo de reproducción que contiene la información requerida se pone a disposición del solicitante a partir del sexto dí­a de presentada la solicitud. En tal supuesto, el ciudadano se acerca a la entidad, cancela el monto, a efectos que la entidad efectúe la reproducción de la información requerida y pueda poner a disposición la información dentro del plazo establecido legalmente. <br/>" +
      " - No se puede negar información cuando se solicite que esta sea entregada en una determinada forma o medio, siempre que el solicitante asuma el costo que suponga el pedido. <br/>" +
      " - En caso de presentación de Recurso de Apelación, el plazo máximo de presentación es de 15 dí­as hábiles, de conformidad al Precedente Vinculante emitido por el Tribunal de Transparencia y Acceso a la Información Pública, según Resolución Nº 010300772020. El plazo máximo de respuesta es de 10 días hábiles, contados a partir de la admisibilidad del recurso por el Tribunal de Transparencia y Acceso a la Información Pública, de conformidad con la Tercera Disposición Complementaria Modificatoria del Decreto Supremo Nº 011-2018-JUS, que incorpora el artículo 16-B en el Reglamento de la Ley de Transparencia y Acceso a la Información Pública. <br/>";
    this.descripcionEntrega = "(2) Se remitirá la información por correo electrónico cuando los archivos adjuntos no sobrepasen el lí­mite de capacidad institucional; para lo cual, deberá consignar un correo electrónico sin problemas técnicos que impida la comunicación con usted (por ejemplo: que el buzón está lleno, que está bloqueado temporalmente y/o posea restricciones para recibir o enviar correos institucionales, etc.). "

    this.descripcionDatosAdiconales = "Completar la siguiente información de contar con la misma (1): <br/>" +
      "(1) Su llenado es opcional; sin embargo, si requiere el enví­o de información por correo electrónico, es necesario completar dicho campo.";
    this.getUnidadOrganica();
    this.llenarDepartamento();
    this.mapListaProvincia = [];
    this.mapListaDistrito = []
    this.obtenerSolicitud(this.date);
    this.itemDepartamento = '';
    this.itemProvincia = '';
    this.itemDistrito = '';
    window.document.title = "Solicitud de Información Pública";
  },
  filters: {
    upper: function (value) {
      return value.toUpperCase();
    }
  },
  methods: {
    regresar() {
      location.reload();
    },
    envioformulario() {
      this.boolVista = false;
      EventBus.$emit("loadershow", true);
      this.$recaptcha("accion").then((token) => {
        this.validarTokenGoogle(token)
      });
    },
    validarTokenGoogle(token) {
      const url = Constantes.rutaTareasComunes + "recaptcha/validacion";
      const params = {
        token: token,
        action: "login"
      }
      axios.post(url, params).then(response => {
        let data = response.data;
        if (data["json Google acceso"]) this.IniciarTramite();
        else this.$swal({
          icon: 'error',
          title: 'Error',
          text: 'Captcha error'
        });
      }).catch(e => console.warn(e))
    },
    IniciarTramite() {
      this.boolFormulario = true;
      if (this.validaFormulario()) {
        var fecha = this.customFormatter(this.date);
        var correoRegistro = this.correo.trim() === '' ? this.correoAcceso : this.correo;
        let dataPost = {};
        dataPost.nombUserRegistra = correoRegistro;
        dataPost.fecRegistra = fecha;
        if (this.rdPersona === 109) {
          dataPost.nomApellidoPaterno = this.aPaterno;
          dataPost.nomApellidoMaterno = this.aMaterno;
          dataPost.nomNombres = this.nombre;
        } else if (this.rdPersona === 110) {
          dataPost.nomNombres = this.rSocial;
        }
        dataPost.numDocumento = this.nroDoc;
        dataPost.numTelefono = this.telefono;
        if (this.celular != null && this.celular.length > 0) dataPost.numCelular = this.celular;
        dataPost.desEmail = this.correo;
        dataPost.desDetalleSolicitud = this.informacion;
        dataPost.ideTipoPersona = this.rdPersona;
        dataPost.ideTipoDocumento = this.rdTipoDoc;
        if (this.itemDepartamento && this.itemDepartamento > 0) {
          dataPost.idDepartamento = this.itemDepartamento;
          dataPost.desDepartamento = this.mapListaDepartamento.find(element => element.value === this.itemDepartamento).text;
        }
        if (this.itemProvincia && this.itemProvincia > 0) {
          dataPost.idProvincia = this.itemProvincia;
          dataPost.desProvincia = this.mapListaProvincia.find(element => element.value === this.itemProvincia).text;
        }
        if (this.itemDistrito && this.itemDistrito > 0) {
          dataPost.idDistrito = this.itemDistrito;
          dataPost.desDistrito = this.mapListaDistrito.find(element => element.value === this.itemDistrito).text;
        }
        dataPost.domicilio = this.domicilio;
        dataPost.desUsuRegistra = correoRegistro;
        dataPost.desObservacion = this.observaciones;
        if (this.unidadOrigen.value !== undefined && this.unidadOrigen.value !== 0) dataPost.desUnidadSel = this.unidadOrigen.text;
        dataPost.idTipoEntrega = this.rdEntrega;

        var dataDocumento = new FormData();
        if (this.adjunto.nombre != null) {
          dataDocumento.append('documento', this.adjunto.archivo);
        }
        dataDocumento.append('dataPost', JSON.stringify(dataPost));
        this.$swal({
          title: "Guardando solicitud...",
          onOpen: () => {
            this.$swal.showLoading();
          }
        });
        axios.post(Constantes.rutaAtencion + 'grabarsolicitudinfpublica', dataDocumento)
          .then(response => {
            EventBus.$emit("loadershow", false);
            this.finish = true
            this.idSolicitud = response.data.data.numRegistro
            this.urlPdfDescargar = response.data.data.urlConstacia
            this.$swal.fire({
              icon: 'success',
              title: 'Solicitud procesada con éxito',
              showConfirmButton: false,
              timer: 1500
            })
          }).catch(e => this.$swal({
            icon: 'info',
            text: 'Error en registro de solicitud.'
          })
        );
      } else {
        this.$swal({
          icon: 'info',
          text: this.texto
        });
        EventBus.$emit("loadershow", false);
        this.boolVista = true;
      }
    },
    getUnidadOrganica() {
      axios.get(Constantes.rutaTramite + 'unidadorganica/-')
        .then(response => {
          var i = 0;
          while (i < response.data.data.length) {
            var unidadorganica = {};
            unidadorganica.value = response.data.data[i].uniorgcod;
            unidadorganica.text = response.data.data[i].uniorgnom;
            this.dataUnidades.push(unidadorganica);
            i++;
          }
          this.unidadOrigen = this.dataUnidades[0];
        })
        .catch(e => console.log(e))
    },
    llenarDepartamento() {
      axios.get(Constantes.API_PERSONA + '/ubigeo/1/0/0/0')
        .then(response => {
          let data = response.data.data;
          this.mapListaDepartamento = [];
          let i = 0;
          while (i < data.length) {
            var departamento = {};
            departamento.value = data[i].indDepartam;
            departamento.text = data[i].desNombre;
            this.mapListaDepartamento.push(departamento);
            i++;
          }
        })
        .catch(e => console.log(e))
    },
    llenarProvincia() {
      axios.get(Constantes.API_PERSONA + '/ubigeo/1/' + this.itemDepartamento + '/0/0')
        .then(response => {
          let data = response.data.data;
          this.mapListaProvincia = []
          let i = 0;
          while (i < data.length) {
            var provincia = {};
            provincia.value = data[i].indProvincia;
            provincia.text = data[i].desNombre;
            this.mapListaProvincia.push(provincia);
            i++;
          }
        })
        .catch(e => console.log(e))
    },
    llenarDistrito() {
      axios.get(Constantes.API_PERSONA + '/ubigeo/1/' + this.itemDepartamento + '/' + this.itemProvincia + '/0')
        .then(response => {
          let data = response.data.data;
          this.mapListaDistrito = []
          console.log(data)
          let i = 0;
          while (i < data.length) {
            var distrito = {};
            distrito.value = data[i].ideUbigeo;
            distrito.text = data[i].desNombre;
            this.mapListaDistrito.push(distrito);
            i++;
          }
        })
        .catch(e => console.log(e))
    },
    obtenerSolicitud(date) {
      this.anho = date.getFullYear();
      axios.get(Constantes.rutaAtencion + 'obtener-solicitudmaxima/324').then(response => {
        this.solicitud = response.data.data;
      }).catch(e => console.log("Error al obtener número de solicitud."));
    },
    getExtensionFile(filePath) {
      var lastDot = filePath.lastIndexOf('.');
      var fileName = filePath.substring(0, lastDot);
      var ext = filePath.substring(lastDot + 1);
      return ext;
    },
    onFileSelected($event) {
      var extension = this.getExtensionFile(event.target.files[0].name);
      if (extension == 'pdf') {
        this.adjunto = {};
        this.adjunto.nombre = event.target.files[0].name;
        this.adjunto.archivo = event.target.files[0];
        this.nombArchivo = this.adjunto.nombre;
        this.cargado = true;
      }
    },
    eliminar() {
      this.adjunto = {};
      this.cargado = false;
      this.nombArchivo = '';
    },
    customFormatter(date) {
      return moment(date).format('YYYY-MM-DD');
    },
    validaFormulario() {
      this.texto = '';
      if (this.rdTipoDoc == 31 && !((this.nroDoc).length == 8)) {
        this.boolFormulario = false;
        this.texto = 'Número de dígitos incorrecto.';
      }
      if (this.rdTipoDoc == 32 && !((this.nroDoc).length == 11)) {
        this.boolFormulario = false;
        this.texto = 'Número de dígitos incorrecto.';
      }
      if (this.rdTipoDoc == 33 && !((this.nroDoc).length >= 8 && (this.nroDoc).length <= 12)) {
        this.boolFormulario = false;
        this.texto = 'Número de dígitos incorrecto.';
      }
      if (this.rdPersona == 109) {
        if (this.nombre.trim() == '' || this.aPaterno.trim() == '' || this.nroDoc.trim() == '' || this.informacion.trim() == '' || this.domicilio.trim() == '' || this.rdEntrega == '') {
          this.boolFormulario = false;
          this.texto = 'Favor de ingresar los campos obligatorios (*).';
        } else {
          this.boolFormulario = true;
        }
      } else if (this.rdPersona == 110) {
        if (this.rSocial.trim() == '' || this.nroDoc.trim() == '' || this.informacion.trim() == '' || this.domicilio.trim() == '' || this.rdEntrega == '') {
          this.boolFormulario = false;
          this.texto = 'Favor de ingresar los campos obligatorios (*).';
        } else {
          this.boolFormulario = true;
        }
      }
      if (this.correo != '') {
        if (!this.validEmail(this.correo.trim())) {
          this.boolFormulario = false;
          if (this.texto == '') this.texto = 'Correo ingresado no válido.';
          else this.texto = this.texto + ' Correo ingresado no válido.';
        } else {
          if (this.boolFormulario == true) this.boolFormulario = true;
        }
      }
      if (this.nroDoc.trim() != '') {
        this.cDigitos = 0;
        if (this.rdTipoDoc == 31 && (this.nroDoc.length > 8 || this.nroDoc.length < 8)) {
          this.cDigitos = 8;
          this.boolFormulario = false;
          if (this.texto == '') this.texto = 'Cantidad de dígitos (' + this.cDigitos + ') de número de documento incorrecto para el tipo seleccionado.';
          else this.texto = this.texto + ' Cantidad de dígitos (' + this.cDigitos + ') de número de documento incorrecto para el tipo seleccionado.';
        } else if (this.rdTipoDoc == 32 && (this.nroDoc.length > 11 || this.nroDoc.length < 11)) {
          this.cDigitos = 11;
          this.boolFormulario = false;
          if (this.texto == '') this.texto = 'Cantidad de dígitos (' + this.cDigitos + ') de número de documento incorrecto para el tipo seleccionado.';
          else this.texto = this.texto + ' Cantidad de dígitos (' + this.cDigitos + ') de número de documento incorrecto para el tipo seleccionado.';
        } else if (this.rdTipoDoc == 33 && (this.nroDoc.length > 12 || this.nroDoc.length < 8)) {
          this.cDigitos = 12;
          this.boolFormulario = false;
          if (this.texto == '') this.texto = 'Cantidad de dígitos (' + this.cDigitos + ') de número de documento incorrecto para el tipo seleccionado.';
          else this.texto = this.texto + ' Cantidad de dígitos (' + this.cDigitos + ') de número de documento incorrecto para el tipo seleccionado.';
        }
      }
      if (this.rdEntrega == 319 && this.correo == '') {
        this.boolFormulario = false;
        if (this.texto == '') this.texto = 'Es necesario el correo electrónico para el tipo de entrega seleccionado.';
        else this.texto = this.texto + ' Es necesario el correo electrónico para el tipo de entrega seleccionado.';
      }
      return this.boolFormulario;
      //this.envioformulario();
    },
    isNumber: function (evt) {
      evt = (evt) ? evt : window.event;
      var charCode = (evt.which) ? evt.which : evt.keyCode;
      if ((charCode > 31 && (charCode < 48 || charCode > 57)) && charCode !== 46) {
        evt.preventDefault();
        ;
      } else {
        return true;
      }
    },
    validEmail: function (email) {
      var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      return re.test(email);
    },
    enviarCorreo() {
      this.$swal({
        title: "Enviando correo...",
        onOpen: () => {
          this.$swal.showLoading();
        }
      });
      const url = Constantes.rutaAtencion + "enviar-correo-acceso-informacion"
      axios.get(url, {
        params: {
          pdfUrl: this.urlPdfDescargar,
          correo: this.enailEnviar,
          idSolicitud: this.idSolicitud

        },
      }).then((response) => {
        this.$swal.fire({
          icon: 'success',
          title: 'Correo enviado con éxito',
          showConfirmButton: false,
          timer: 1500
        })
      });
    },

  }
};
</script>
<style lang="scss" scoped>
.btn-eliminar-SIP {
  margin-top: 4px;
  border: 1px solid #F44336;
  color: #F44336;
  transition: 0.5s all;
  margin: 0;

  &:hover {
    background: #F44336;
    color: white;
    transition: 0.5s all;
  }
}

.form-group {
  margin-bottom: 12px;
}

.form-control:focus {
  background-color: #fff;
  border-color: #eaf0f9;
}

input::placeholder {
  color: #6c757da6;
  font-size: 14px;
  font-family: "CamberRegular", sans-serif !important;
  font-weight: 400;
}

.alert-info {
  color: #0c5460;
  background-color: #eaf0f9;
  border-color: #dbe9ff;
}

.box-btn-registroAcceso {
  text-align: center;
}

.btn-registroAcceso {
  background: #f8981d !important;
  color: #fff !important;
  border: solid 1px #f8981d !important;
  box-shadow: none !important;
  width: 280px !important;
  height: 50px !important;
  font-size: 18px;
}

.btn-enviarAcceso {
  background: #428bca !important;
  color: #e9eff9 !important;
  border: solid 1px #e9eff9 !important;
  box-shadow: none !important;
  width: 120px !important;
  height: 35px !important;
  font-size: 14px;
}

.descripcion-content {
  height: 100%;
  position: relative;
  margin-bottom: 100px;

  .mobile-block {
    display: none;
  }

  .top-title {
    padding-top: 24px;
    padding-bottom: 24px;
    display: flex;
    align-content: space-between;

    .titulo-form-regitroAcceso {
      color: #0078cf;
      text-align: center;
    }
  }

  ul {
    margin-left: 0px;
    padding-left: 0px;
    list-style: none;
    border: solid 1px #eaeaea;
    border-radius: 10px;
    overflow: hidden;
    margin-bottom: 70px;

    li {
      background: #eaf0f9;
      border-bottom: solid 1px #eaeaea;

      > p.principal {
        color: #636363;
        background: #fff;
        margin-bottom: 0px;
        padding: 10px;
      }

      > span {
        padding: 10px;
        display: block;
      }
    }
  }

  h5 {
    font-size: 15px;
    color: #0078cf;
    font-weight: normal;
  }

  .area {
    color: #2db5c3;
    font-weight: 500;
    text-transform: capitalize;
  }

  .full {
    display: flex;
    justify-content: flex-end;
    align-items: center;

    .conten-icon {
      padding-left: 3px;
    }

    &.full2 {
      height: initial;

      .conten-letter {
        span.change p.nom {
          white-space: initial;
          overflow: inherit;
          text-overflow: initial;
        }

        .change {
          padding-left: 0px;
          display: flex;
          align-items: center;

          .closee {
            top: inherit;
            justify-content: center;
          }

          img {
            position: relative;
            left: initial;
            top: initial;
            padding-left: 5px;
            padding-right: 5px;
          }

          a {
            display: flex;
            align-items: center;
            justify-content: space-between;
          }
        }
      }
    }
  }

  &:hover {
    .closee {
      display: block !important;
      background: #fff;
    }

    .conten-letter {
      position: relative;

      input {
        height: 18px;
        cursor: pointer;
      }

      span {
        display: inline-block;
        cursor: pointer;
      }
    }
  }

  .conten-letter {
    position: relative;

    span.change {
      display: inline-block;
      position: relative;
      padding-left: 42px;

      p.nom {
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        margin-bottom: 0px;
        max-width: 100px;
      }

      img {
        position: absolute;
        left: 4px;
        top: 0px;
      }
    }

    .change {
      padding-top: 5px;
      padding-bottom: 5px;
      font-size: 12px;
      line-height: normal;
      padding-left: 10px;
      padding-right: 10px;
      border-radius: 10px;
      background: #fff;
      color: #9c9c9c;
      border: solid 1px #ddd;

      & ~ input {
        display: none;
      }
    }
  }

  .content-requi {
    margin-top: 15px;
    padding-bottom: 15px;

    .requi-title {
      padding-left: 25px;
      padding-top: 20px;
      padding-right: 25px;

      label {
        margin-bottom: 0px;
        padding-bottom: 0px;
      }
    }

    ul {
      padding-left: 20px;
      list-style: none;
      padding-top: 25px;
      padding-right: 20px;

      li {
        flex-flow: row;
        align-items: flex-start;
        display: flex;
        width: 100%;
        margin-bottom: 12px;

        .left-img {
          margin-right: 10px;
          width: 25px;

          img {
            max-width: 20px;
          }
        }

        &:hover {
          .closee {
            display: block !important;
            background: #fff;
          }

          .conten-letter {
            position: relative;

            input {
              height: 18px;
              cursor: pointer;
            }

            span {
              display: inline-block;
              cursor: pointer;
            }
          }
        }

        .conten-adj {
          width: 385px;
          line-height: normal;
        }

        .full {
          max-width: 193px;
          width: 100%;
          text-align: right;
          height: 20px;

          &.full2 {
            max-width: inherit;
          }
        }

        .conten-letter {
          position: relative;

          span.change {
            display: inline-block;
            position: relative;
            padding-left: 42px;

            p.nom {
              white-space: nowrap;
              overflow: hidden;
              text-overflow: ellipsis;
              margin-bottom: 0px;
              max-width: 100px;
            }

            img {
              position: absolute;
              left: 4px;
              top: 0px;
            }
          }

          .change {
            padding-top: 5px;
            padding-bottom: 5px;
            font-size: 12px;
            line-height: normal;
            padding-left: 10px;
            padding-right: 10px;
            border-radius: 10px;
            background: #fff;
            color: #9c9c9c;
            border: solid 1px #ddd;

            & ~ input {
              display: none;
            }
          }
        }

        .conten-icon {
          span {
            width: 10px;
            height: 10px;
            border-radius: 50%;
            display: inline-block;

            &.er {
              background: #ff6464;
            }

            &.su {
              background: #bed730;
            }

            &.pe {
              background: #989898;
            }
          }
        }
      }

      &.full-list {
        padding-left: 26px;
        padding-top: 5px;

        li {
          .full {
            width: 100%;
            text-align: left;
          }

          p.nom {
            width: 100%;
            max-width: 100% !important;
          }
        }
      }
    }
  }

  .custom-file-input {
    position: absolute;
    left: -2px;
    top: -4px;
    cursor: pointer;
    height: 27px;
  }

  .add {
    .custom-file-input {
      position: absolute;
      right: 0;
      cursor: pointer;
      height: 35px;
      width: 181px;
      left: inherit;
      top: -6px;
    }
  }
}

$custom-file-text: (
  en: "Browse",
  es: "Elegir"
);
@media only screen and (max-width: 600px) {
  .descripcion-content {
    ul {
      margin-bottom: 0px;
    }

    .mobile-block {
      padding-top: 40px;
      padding-bottom: 60px;
      text-align: center;
      display: block;
    }

    .top-title {
      flex-direction: column-reverse;

      > div {
        width: 100% !important;
      }

      h5 {
        width: 100%;
      }
    }

    .mobile-hide {
      display: none;
    }


  }
}
</style>



